import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

const routes = [{
  path: '/',
  redirect: '/wyllogin',
},
{
  path: '/wyllogin',
  name: 'wyllogin',
  component: () => import('../components/Login.vue'),
  mata: {
    title: "登录页面",
  }
},
{
  path: '/Home',
  name: 'Home',
  redirect: "/welcome",
  component: () => import('../components/Home.vue'),
  mata: {
    title: "Home主页面",
  },
  children: [{
    path: '/Welcome',
    name: 'Welocme',
    component:() => import('../components/Welcome.vue'),
  },
  {
    path: '/Users',
    name: 'Users',
    component:() => import('../components/User/Users.vue'),
  },
  {
    path: '/Rights',
    name: 'Rights',
    component:() => import('../components/Power/Rights.vue'),
  },
  {
    path: '/Roles',
    name: 'Roles',
    component:() => import('../components/Power/Roles.vue'),
  },
  {
    path: '/Categories',
    name: 'Categories',
    component:() => import('../components/Goods/Categories.vue'),
  },
  {
    path: '/reports',
    name: 'Report',
    component:() => import('../components/Report/Report.vue'),
  },
  ]
}
].map(m => {

  // 使用 if 进行判断   m里面含有meta 并且 m里面的meta 里面还有 title   就 返回 本身   不是的话  就让 m.meta 为空   并且让 name值 付给  给  m.meta
  if (m.meta && m.meta.title) {
    return m
  } else {
    m.meta = {}
    m.meta.title = m.name
    return m
  }

})

const router = new VueRouter({
  routes
})

//挂载路由导航守卫
router.beforeEach((to, from, next) => {
  if (to.path == '/wyllogin') {
    return next()
  }
  let tokenstr = window.sessionStorage.getItem('token')
  if (!tokenstr) {
    return next('/wyllogin');
  } else {
    next();
    document.title = to.meta.title
  }
})

export default router
